<template>
  <div class="container">
    <Header type="back" :title="t('index.')"></Header>
    <div class="share">
      <div class="content">
        <div class="qrcode" style="height:172px;">
          <img v-if="qrcode" :src="qrcode" alt="">
        </div>
        <div class="link">
          {{origin+'/#/?intro='+address}}
        </div>
        <div class="btn-wrap">
          <div class="btn copy-cur-href" :data-clipboard-text="origin+'/#/?intro='+address">{{t('index.')}}</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import QRCode from 'qrcode'
import Clipboard from "clipboard";
import { Toast } from "vant";
export default {
  components: {
    Header
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      balance: 0,
      qrcode: '',
      origin: location.origin,
      initContract: () => {
        data.initData()
      },
      initData: () => {
        data.generateQR()
      },
      generateQR: async () => {
        try {
          data.qrcode = await QRCode.toDataURL(data.origin + '/#/?intro=' + data.address, {
            width: 172,
            margin: 2
          })
        } catch (err) {
          console.error(err)
        }
      }
    })
    onMounted(() => {
      var clipboardHref = new Clipboard(".copy-cur-href");
      clipboardHref.on("success", function (e) {
        Toast(t("share."));
        e.clearSelection();
      });
      clipboardHref.on("error", function () {
        Toast(t("share."));
      });
    });
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.container {
  // background: url(~@/assets/images/share-bg.webp) 0 0 no-repeat;
  background-size: cover;
  min-height: 100vh;
  box-sizing: border-box;
}
.share {
  padding: 0 9.333vw 11.2vw;
  .content {
    text-align: center;
    box-sizing: border-box;
    margin: 0 auto;
    padding: 48.5vw 0 0;
    .t {
      //   background: url(~@/assets/images/t-bg.webp) no-repeat;
      width: 28vw;
      height: 8vw;
      margin: 0 auto;
      line-height: 8vw;
      text-align: center;
    }
    .link {
      background: rgba(63, 72, 92, 0.3);
      border-radius: 2.133vw;
      padding: 5.867vw 0 10vw;
      word-break: break-all;
      font-size: 15px;
      font-weight: 500;
      color: #ffffff;
      line-height: 5.067vw;
    }
    .btn-wrap {
      margin-top: 0;
      .btn {
        width: initial;
        padding: 0 9.867vw;
        height: 9.067vw;
        background: url(~@/assets/images/button6.webp) no-repeat;
        background-size: 100% 100%;
        line-height: 9.067vw;
        margin: 0 auto;
      }
    }
  }
}
</style>
<style lang="less">
.qrcode {
  img {
    margin: 0 auto;
    display: block;
  }
}
</style>
